/* box 스타일 */
.main_box{
  position: relative;
  width: 600px;
  height: 600px;
  overflow: hidden;
  background-color: #fff;
  display: flex;
  flex-wrap: wrap;
}
.child_box{
  position: relative;
  width: 50%;
  height: 50%;
  border: 2px solid #000;
  box-sizing: border-box;
}
/* box 위치 */
.middle{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
}
.left_bot{
  position: absolute;
  left: 50%;
  bottom: 0;
  border: none;
}
.middle_bot{
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
}
.left_bot_out{
  position: absolute;
  right: 100%;
  top: 100%;
  transform: translate(51%,-50%);
}
.middle_left{
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
/* 색깔 */
.pink{
  background-color: #ff00ff;
}
.black{
  background-color: #000;
}
.yellow{
  background-color: #ffff00;
}
.green{
  background-color: #00ff00;
}
.white{
  background-color: #fff;
}
.blue{
  background-color: blue;
}
.skyblue{
  background-color: #00ffff;
  border-bottom: none;
}
.red{
  background-color: red;
  border-left: none;
  border-bottom: none;
}
/* box z-index */
.z-9999{
  z-index: 9999;
}